<template>
  <div id="app">
    <div v-if='page.length && !showResult'>
      <div class='title'>{{page[index].title}}</div>
      <div v-for="(item,serial) in page[index].list" :key='item.id'>
        <Question
        :question='item'
        :last='serial == page[index].list.length-1'
        @addAnswer="addAnswer">
        </Question>
      </div>
      <Button type="primary" size="large" @click='next'>
        <span v-if='index !== page.length-1'>Next</span>
        <span v-if='index == page.length-1'>Submit</span>
      </Button>
    </div>
    <div v-if='showResult' class='result'>
      This is your MTurk code: {{answer.userID}}. Thanks :)
    </div>
    <Spin size="large" v-if='page.length==0' class='spin'></Spin>
    <BackTop></BackTop>
  </div>
</template>

<script>
import Question from "./components/question.vue";
import Service from "./services/service.js";

export default {
  name: "app",
  components: {
    Question
  },
  data: function() {
    return {
      index: 0,
      splice: 0,
      page: [],
      otherScore: 0,
      answer: {
        random: "",
        userID: "",
        score: 0,
        list: []
      },
      showResult: false
    };
  },
  mounted: function() {
    /**
     * random 随机生成 0、1、2、3  对应四套题库的索引值
     */
    this.getQuestion();
  },
  methods: {
    getQuestion: function() {
      let random = parseInt(Math.random() * 4);
      let random1 = parseInt(Math.random() * 3);
      this.answer.random = [random, random1];
      this.$axios("");
      this.$axios
        .get("/api/getJson")
        .then(data => {
          if (typeof data === "string") {
            data = JSON.parse(data).data;
          }
          console.log('success',data)
          let q = data.question_all[random0][random1];
          let page = [];
          let index = 0;
          let json = {
            title: "Human Robot Trust Experiment",
            list: []
          };
          q.forEach((item, index1) => {
            if (item.q.type !== 5) {
              json.list.push(item);
            } else {
              if (json.list.length) {
                page[index] = json;
                index++;
              }
              json = {
                title: item.q.title,
                list: []
              };
            }
          });
          this.page = page;
          this.Idx = data.logic_Idx;
          window.console.log(this.page);
        })
        .catch(() => {
          this.$Message.error({
            content: "get question failed"
          });
        });
    },
    addAnswer: function(answer) {
      let exist = false;
      this.answer.list.forEach((item, index) => {
        if (item.id == answer.id) {
          this.answer.list[index] = answer;
          exist = true;
        }
      });
      if (!exist) {
        this.answer.list.push(answer);
      }
    },
    next: function() {
      /**
       * 校验答案是否和可回答题目数一致
       */
      let res = this.checkAnswer();
      if (!res.result) {
        this.$Message.error({
          content: res.msg
        });
        return;
      }
      this.getOtherScore();
      if (this.index == this.page.length - 1) {
        /**
         * 提交操作
         */
        this.getAllScore();
        this.submit();
      } else {
        let idx = this.Idx;
        console.log("logic idx", idx);
        let answer = this.answer.list.slice(this.splice);
        answer.forEach((item, index) => {
          let id = item.id.split("_")[2];
          let exist = idx.some(item1 => {
            if (item1 == id) {
              this.answer.list.forEach((child, index1) => {
                if (child.id == item.id) {
                  this.splice = index1 + 1;
                  console.log(this.splice);
                }
              });
              return true;
            }
            return false;
          });
          if (exist) {
            if (item.value != 0) {
              this.page[this.index + 1].hide = true;
            } else {
              this.page[this.index + 2].hide = true;
            }
          }
        });
        this.index++;
        if (this.page[this.index].hide) {
          this.index++;
          console.log(this.page, this.index);
        }
        this.$nextTick(function() {
          window.scrollTo(0, 0);
        });
      }
    },
    checkAnswer: function() {
      let res;
      let length = this.answer.list.length;
      let topicLength = 0;
      this.page.forEach((item, index) => {
        if (index <= this.index) {
          if (!item.hide) {
            item.list.forEach(child => {
              let type = child.q.type;
              if (type == 1 || type == 2 || type == 3) {
                topicLength++;
              }
            });
          }
        }
      });
      if (length == topicLength) {
        let wrong = this.answer.list.some(item => {
          return item.score == -1;
        });
        if (wrong) {
          res = {
            result: false,
            msg: "Please read carefully and chooseagain"
          };
        } else {
          res = {
            result: true
          };
        }
      } else {
        res = {
          result: false,
          msg: "Please answer all questions!"
        };
      }
      return res;
    },
    getAllScore: function() {
      let list = this.answer.list;
      list.forEach(item => {
        if (item !== "null") {
          this.answer.score += item.score;
        }
      });
    },
    getOtherScore: function() {
      //将描述题 type==0 or type == 4的所得分加进来
      let index = this.index;
      let page = this.page;
      let list = page[index].list;
      list.forEach(item => {
        if (item.q.type == 0 || item.q.type == 4) {
          if (item.score) {
            let type = Object.prototype.toString
              .call(item.score)
              .slice(8, -1)
              .toLowerCase();
            if (type == "string" || type == "number") {
              this.answer.score += item.score;
            } else {
              window.console.error("查看下score数据结构");
            }
          }
        }
      });
    },
    submit: function() {
      let id = new Date().getTime().toString();
      let data = Object.assign(this.answer, {
        userID: id.slice(1)
      });
      console.log(data);
      this.$axios
        .post("/api/submit", data)
        .then(() => {
          this.$Message.success({
            content: "submit success"
          });
          this.showResult = true;
        })
        .catch(() => {
          this.showResult = true;
          this.$Message.error({
            content: "submit failed"
          });
        });
    }
  }
};
</script>

